<template>
	<box-item title="绿色认证">
		<div class="circle-line-wrap">
			<circle-line :options="circleLineOptions" />
		</div>
	</box-item>
</template>

<script>
import CircleLine from '@/components/common/circle-line';
import BoxItem from '@/components/common/BoxItem';
import {areaLevelCorAreaId} from '@/common/fn';

const farmAreaInfo = areaLevelCorAreaId();

export default {
	name: 'GreenCertification.vue',
	data() {
		return {
			circleLineOptions: {
				centerTitle: '蔬菜类认证率',
				leftTitle: '认证面积',
				rightTitle: '认证主体数',
				centerNum: '%',
				leftNum: '亩',
				rightNum: '家'
			}
		};
	},
	props: {},
	created() {
		this.renderInfo();
	},
	methods: {
		getAllCateByArea() {
			return this.$ajax
				.post(this.$api.safeSupervise.allCateByArea, {
					farmAreaId: [farmAreaInfo.farmAreaId],
					farmAreaType: farmAreaInfo.farmAreaType
				})
				.then(({data: res}) => {
					if (res.respCode === 0) {
						return res.obj;
					}
					throw new Error(res);
				})
				.catch(e => {
					// console.log(e);
					throw e;
				});
		},
		async renderInfo() {
			try {
				const res = await this.getAllCateByArea();
				this.circleLineOptions.leftNum = `${res.vegetablesCertifiLandArea}万亩`.replace(/(undefined|null)/g, '');
				this.circleLineOptions.centerNum = `${res.vegetablesCertifiRate}%`.replace(/(undefined|null)/g, '');
				this.circleLineOptions.rightNum = `${res.vegetablesCertifiCompany}家`.replace(/(undefined|null)/g, '');
			} catch (e) {
				console.log(e);
			}
		}
	},
	computed: {},
	components: {
		CircleLine,
		BoxItem
	}
};
</script>

<style scoped lang="less">
.circle-line-wrap {
	box-sizing: border-box;
	width: 100%;
	max-width: calc(420px * 1.5);
	margin: 0 auto;
	display: flex;
	align-items: center;
	height: 100%;
}
</style>



